<template>
  <v-ons-page>
    <custom-toolbar title="Business" :action="toggleMenu"></custom-toolbar>
    <div v-show="loading.business">
      <ons-progress-bar indeterminate></ons-progress-bar>
    </div>
    <div v-show="!loading.business">
        <v-ons-card v-for="(article, key) in articles_business" :key="key" :id="key">
          <ul>
            <li class="newsTitle">{{ article.title }}</li>
          </ul>
          <img :src="article.urlToImage" alt="Onsen UI" style="width: 100%" v-show="article.urlToImage">
          <div class="content">
            <ul>
              <li><span class='badge'>{{ article.publishedAt.split("T")[0] }}</span></li>
              
              <li>{{ article.description }}</li>
              <li><br><ons-button modifier="default" @click="openNewsTab(article.url,key)">READ MORE</ons-button></li>
            </ul>
          </div>
        </v-ons-card>
    </div>
  </v-ons-page>
</template>

<script>
import Toolbar from './Toolbar'
export default {
  name: 'Business',
  components:{
    'custom-toolbar':Toolbar
  },
  props: ['toggleMenu','articles_business','loading'],
  data () {
    return {
      
    }
  },
  computed: {

  },
  methods : {
    openNewsTab(url,key){
      sessionStorage.setItem('lastClickedNews',key)
      window.open(url, '_blank', 'location=no,hidden=yes,closebuttoncaption=Done,toolbar=no')
    }
  },
  mounted : function (){
    sessionStorage.setItem('lastViewdPage','Business')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul{
  padding: 0px;
  list-style: none;
  margin: 0px;
}
.badge{
  font-weight: bold;
  text-transform: uppercase;
}
li{
  clear: both;
}
li.newsTitle{
  color:#555;
  font-weight: bolder;
}
</style>



