<template>
  <v-ons-splitter>
    <v-ons-splitter-side swipeable width="150px" collapse="" side="left" :open.sync="openSide">
      <v-ons-page>
        <v-ons-list>
          <v-ons-list-item v-for="(page, index) in pages" :key="index" tappable modifier="chevron" @click="currentPage = page; openSide = false">
            <div class="center">{{ page }}</div>
          </v-ons-list-item>
        </v-ons-list>
      </v-ons-page>
    </v-ons-splitter-side>

    <v-ons-splitter-content>
      <component :is="currentPage" :toggle-menu="() => openSide = !openSide" :articles_business="articles_business" :articles_entertainment="articles_entertainment" :articles_health="articles_health" :articles_science="articles_science" :articles_sports="articles_sports" :articles_technology="articles_technology" :loading="loading"></component>
    </v-ons-splitter-content>
  </v-ons-splitter>
</template>

<script>
import db from '@/firebase/init'

import Business from './Business'
import Entertainment from './Entertainment'
import Health from './Health'
import Science from './Science'
import Sports from './Sports'
import Technology from './Technology'
import About from './About'
export default {
  name: 'Main',
  components:{
    Business,
    Entertainment,
    Health,
    Science,
    Sports,
    Technology,
    About
  },
  data () {
    return {
      currentPage: '',
      pages : ['Business','Entertainment','Health','Science','Sports','Technology','About'],
      openSide : false,

      articles_business:[],
      articles_entertainment:[],
      articles_health:[],
      articles_science:[],
      articles_sports:[],
      articles_technology:[],

      loading:{
        'business':true,
        'entertainment':true,
        'health':true,
        'science':true,
        'sports':true,
        'technology':true
      }
    }
  },
  methods:{
    scrollToLastView(){
      var key = sessionStorage.lastClickedNews
      if(key)
        setTimeout(function(){ location.href = "#"+key }, 1500)
    },
    fetchData(){
      this.fetchByCategory('business')
      this.fetchByCategory('entertainment')
      this.fetchByCategory('health')
      this.fetchByCategory('science')
      this.fetchByCategory('sports')
      this.fetchByCategory('technology')
    },
    fetchByCategory(category){
      var newsRef = db.ref('news/'+category)
      if(sessionStorage.getItem('snapshot_'+category)){
        this['articles_'+category] = Object.values(JSON.parse(sessionStorage['snapshot_'+category]))
        this.loading[category] = false
      } else {
        newsRef.once('value', function(snapshot) {
          // turn response object into array
          sessionStorage.setItem('snapshot_'+category,JSON.stringify(snapshot.val()))
          this['articles_'+category] = Object.values(snapshot.val())
          this.loading[category] = false
        }.bind(this))
      }
    },
    showPage(){
      var lastViewdPage = sessionStorage.getItem("lastViewdPage")
      if(!lastViewdPage){
        lastViewdPage = 'Business'
      }
      this.currentPage = lastViewdPage
    }
  },
  computed: {

  },
  created : function (){
    this.showPage()
    this.scrollToLastView()
    this.fetchData()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>



